import React from "react";
import { Stack, Select } from "@chakra-ui/react";
function Filters({ filterField, setFilterFields, attributes }) {
  const onSelectChange = (field, value) => {
    setFilterFields((prev) => ({ ...prev, [field]: value }));
  };

  return (
    <Stack direction="row">
      <SelectFilter
        options={attributes.ownerEmuns}
        value={filterField.owner}
        onChange={(value) => onSelectChange("owner", value)}
      />
      <SelectFilter
        options={attributes.visaEmuns}
        value={filterField.visa}
        onChange={(value) => onSelectChange("visa", value)}
      />
      <SelectFilter
        options={attributes.statusEmuns}
        value={filterField.status}
        onChange={(value) => onSelectChange("status", value)}
      />
    </Stack>
  );
}

const SelectFilter = ({ options, value, onChange }) => (
  <Select value={value} onChange={(e) => onChange(e.target.value)}>
    <option value="">All</option>
    {options.map((value, index) => (
      <option key={index} value={value}>
        {value}
      </option>
    ))}
  </Select>
);

export default Filters;
